<template>
   <div :id="id" :style="{height:chartHeight,width:chartWidth}" class="scatter"></div>
</template>

<script>
import echarts from 'echarts'
export default {
 name: 'Scatter',
 props:{
     id:{
         type:String,
         default:'scatter'
     },
     chartHeight:{
         type:String,
         default:'200px'
     },
     chartWidth:{
         type:String,
         default:'100%'
     },
     scatterData:{
         type:Array,
         default(){
             return []
         }
     },
      targetData:{
          type:String,
          default:null
     }
 },
 data() {
 return {
     chart:null
    }
 },
 created(){},
 mounted(){
    this.initChart(); 
 },
 watch:{
     scatterData(curVal,oldVal){
         this.initChart(); 
     }
 },
 methods:{
     initChart(){
         const self=this;
        self.chart = echarts.init(document.getElementById(self.id));
        self.chart.setOption(self.getSeriesBarOption());
        window.addEventListener('resize', function(){
            if(self.chart){
                self.chart.resize()
            }
        })
     },
     getSeriesBarOption(){
         const self=this
         return {
             title : {
                text: ''
            },
            grid: {
                left: '3%',
                right: '7%',
                bottom: '3%',
                containLabel: true
            },
            tooltip : {
                showDelay : 0,
                formatter : function (params) {
                    if (params.value.length > 1) {
                        return params.name + '<br/>'
                        + params.marker
                        + params.value[0] + ': '
                        + params.value[1];
                    }
                    else {
                        return params.name + '<br/>'
                        + params.marker
                        + params.name + ': '
                        + params.value;
                    }
                },
                axisPointer:{
                    show: true,
                    type : 'cross',
                    lineStyle: {
                        type : 'dashed',
                        width : 1
                    }
                }
            },
            toolbox: {
            　show: true,
            　　feature: {
            　　　　saveAsImage: {
            　　　　show:true,
            　　　　excludeComponents :['toolbox'],
            　　　　pixelRatio: 2
            　　　　}
            　　}
            },
            legend: {},
            xAxis : [
                {
                    type: 'category',
                    name: '姓名',
                    scale:true,
                    // axisLabel : {
                    //      textStyle: {
                    //         color: '#4a4a4a',
                    //     },
                    //     formatter: '{value}'
                    // },
                    // splitLine: {
                    //     show: false
                    // },
                     splitLine: {
                        lineStyle: {
                            show: true,
                            type: 'dashed'
                        }
                    },
                    // 坐标轴刻度标签的相关设置
                    axisLabel: {
                        "interval": 0,
                        rotate: 45,
                        textStyle: {
                            fontSize: 12,
                            color: '#4a4a4a'
                        },
                        formatter: '{value}'
                    },
                    splitLine: {
                        show: false
                    },
                }
            ],
            dataZoom: [{
                type: 'slider',
                show: true,
                xAxisIndex: [0],
                left: '7%',
                bottom: -25,
                start: 0,
                end: 90 //初始化滚动条
            }],
            yAxis : [
                {
                    type : 'value',
                    name:'达成度',
                    scale:true,
                    min: function(value) {
                        return 0;
                    },
                    max: function(value) {
                        return value.max < 1 ? 1 : value.max;
                    },
                    axisLabel : {
                        textStyle: {
                            color: '#4a4a4a',
                        },
                        formatter: '{value}'
                    },
                    splitLine: {
                        show: false
                    }
                }
            ],
            color:['rgb(129, 227, 238)'],
            series : [
                {
                    name:'',
                    type:'scatter',
                    data:self.scatterData,
                    markArea: {
                        silent: true,
                        itemStyle: {
                            normal: {
                                color: 'transparent',
                                borderWidth: 1,
                                borderType: 'dashed'
                            }
                        }
                    },
                    markPoint : {
                        data : [
                            {type : 'max', symbolSize: 50},
                            {type : 'min', symbolSize: 40}
                        ]
                    },
                    markLine : {
                        data : [
                            {type : 'average', name: '平均值', lineStyle: {normal: {type: 'dashed',color: '#008000'}}},
                            { 
                                yAxis: self.targetData,  
                                name: '期望值', 
                                tooltip:{
                                    show:true, 
                                    formatter: function (params) {
                                        return params.name + '<br/>'
                                        + params.marker
                                        + params.value
                                      }
                                    }, 
                                    lineStyle: {normal: {type: 'dashed',color: '#DC143C'}
                                }
                            },
                        ]
                    }
                },
            ]
         }
     }
 },
 beforeDestroy() {
      if (!this.chart) {
        return
      }
      this.chart.dispose();
      this.chart = null;
    }
}
</script>

<style lang='scss' scoped>
</style>



