<template>
   <div :id="id" :style="{height:chartHeight,width:chartWidth}" class="lineMarker"></div>
</template>

<script>
import echarts from 'echarts'
export default {
 name: 'LineMarker',
 props:{
     id:{
         type:String,
         default:'LineMarker'
     },
     chartHeight:{
         type:String,
         default:'200px'
     },
     chartWidth:{
         type:String,
         default:'100%'
     },
     lineData:{
         type:Object,
         default(){
             return {}
         }
     },
     color:{
         type:String,
         default:'#0188fd'
     }
 },
 data() {
 return {
     chart:null
 }
 },
 created(){},
 mounted(){
    this.initChart(); 
 },
 watch:{
     seriesBarData(curVal,oldVal){
         this.initChart(); 
     }
 },
 methods:{
     initChart(){
         const self=this;
        self.chart = echarts.init(document.getElementById(self.id));
        self.chart.setOption(self.getSeriesBarOption());
        window.addEventListener('resize', function(){
           if(self.chart){
                self.chart.resize()
            }
        })
     },
     getSeriesBarOption(){
         const self=this
         return {
                title: {
                    text: '',
                    subtext: ''
                },
                tooltip: {
                    trigger: 'axis',
                    formatter:function (params) {
                        return  params[0].name + "<br/>"+ params[0].marker + "达成度：" +params[0].value;
                        }
                },
                legend: {},
                toolbox: {
                　show: true,
                　　feature: {
                　　　　saveAsImage: {
                　　　　show:true,
                　　　　excludeComponents :['toolbox'],
                　　　　pixelRatio: 2
                　　　　}
                　　}
                },
                xAxis:  {
                    type: 'category',
                    boundaryGap: false,
                    data: self.lineData.xAxisdData || [],
                    axisLabel: {
                        //坐标轴刻度标签的相关设置。this.courseNames,
                        interval: 0,
                        rotate: 45 //代表逆时针旋转45度
                    }
                },
                yAxis: {
                    type: 'value',
                    axisLabel: {
                        formatter: '{value}'
                    }
                },
                series: [
                    {
                        name:'',
                        type:'line',
                        itemStyle : {  
                                normal : {  
                                    color:self.color,
                                    lineStyle:{  
                                        color:self.color
                                    }  
                                }  
                            }, 
                        data:self.lineData.seriesData || []
                    }
                ]
         }
     }

 },
 beforeDestroy() {
      if (!this.chart) {
        return
      }
      this.chart.dispose();
      this.chart = null;
    }
}
</script>

<style lang='scss' scoped>
</style>



