<template>
<div class="center" id="teacherResults">
    <div class="center_breadcrumb">
        <Crumb @headInfo="headInfo" />
    </div>
    <div class="center_content">
        <div class="center_content_search">
            <p class="t_p">请先按条件查询您所要查询的学年、班级</p>
            <div class="center_content_search_left t_s">
                <div class="t_box">
                    <div class="search-input-suffix gap">
                        <label>课程名称：</label>
                        <el-select v-model="page.courseId" placeholder="请选择课程名称" @change="courseIdSelect">
                            <el-option v-for="item in teacherlist" :key="item.id" :label="item.name" :value="item.id">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="search-input-suffix gap">
                        <label>所属学年：</label>
                        <el-select v-model="page.grade" @change="gradeSelect" placeholder="请选择所属学年">
                            <el-option v-for="item in gradeOptions" :key="item" :label="item" :value="item">
                            </el-option>
                        </el-select>
                    </div>
                    <div class="search-input-suffix gap">
                        <label>所属班级：</label>
                        <el-select v-model="page.className" multiple placeholder="请先选择所属班级 可多选" style="width: 306px;">
                            <el-option v-for="item in classOptions" :key="item" :label="item" :value="item">
                            </el-option>
                        </el-select>
                    </div>
                </div>
            </div>
            <div class="t_b">
                <span @click="search">查 询</span>
            </div>
        </div>
        <div v-if="!isShow" class="defaultPage">
            <img src="@/assets/img/img-chengjifenxi.png" alt="">
            <p>选择学年、班级查看分析</p>
        </div>
        <div v-if="isShow" class="teacherResults_tab">
            <template v-for="(vo, index) in list">
                <span @click="tabClick(index)" :key="index" :class="{active: activeIndex === index}" class="t_btn">
                    {{vo.courseName}}
                </span>
                
            </template>
        </div>
        <div v-if="isShow" class="teacherResults_container">
            <div class="teacherResults_container_body">
                <div class="teacherResults_container_title">
                    <span>{{tableData.courseName}}</span>
                    <div style="float: right;padding: 0px 20px;border:1px solid rgba(238,238,238,1);border-radius: 14px;margin-right: 45px;cursor: pointer;line-height: 28px;cursor:pointer;" @click="cahngejump">
                        <SvgIcon icon-class="icondaochu-excel"></SvgIcon>&nbsp;&nbsp;<label style="font-size:14px;color:#ccc;cursor: pointer;">导出报告</label>
                    </div>
                    <div class="teacherResults-dv" v-show="falgechange">
                        <ul>
                            <li @click="educe">课程目标达成度计算过程表</li>
                            <li @click="educe1">课程质量评价报告</li>
                        </ul>
                    </div>
                </div>
                <div class="teacherResults_container_content">
                    <ul>
                        <li><b>开课学期:</b><span>{{tableData.term}}</span></li>
                        <li><b>课程类型:</b><span>{{tableData.classification}}</span></li>
                        <li><b>考核方式:</b><span>{{tableData.examine}}</span></li>
                        <li><b>学时:</b><span>{{tableData.period}}</span></li>
                        <li><b>学分:</b><span>{{tableData.credit}}</span></li>
                        <li><b>任课老师:</b><span>{{tableData.teacherName}}</span></li>
                    </ul>
                </div>
            </div>
            <div class="teacherResults_container_body">
                <div class="teacherResults_container_title">
                    <span>课程成绩分析</span>
                </div>
                <div class="teacherResults_container_content">
                    <p class="zanwu" v-if="courseAnalysis.length === 0">暂无数据</p>
                    <div class="graph" style="margin-top: 15px;display: flex;" v-for="(item, index) in courseAnalysis" :key="index">
                        <div class="graph_dv1" :style="{height:tableHeight}">
                            <p class="graph_dvp">{{item.examType}}</p>
                        </div>
                        <table cellspacing="0" cellpadding="0" border="0" class="el-table el-table--border">
                            <tr>
                                <th style="width:100px;text-align:center;">项目</th>
                                <th style="width:200px;text-align:center;">考核总分</th>
                                <th style="width:500px;text-align:center;">支撑课程教学目标</th>
                                <th style="width:400px;text-align:center;">支撑毕业要求和指标点</th>
                            </tr>
                            <div >

                            </div>
                            <tr v-for="(co, index) in item.result" :key="index"  >
                                <td>{{co.questionName}}</td>
                                <td>{{co.total}}</td>
                                <td>{{co.courseGold}}</td>
                                <td>毕业要求{{co.index_point_code}}</td>
                            </tr>
                        </table>
                    </div>
                </div>
            </div>
            <div class="teacherResults_container_body">
                <div class="teacherResults_container_title">
                    <span>课程达成分析</span>
                </div>
                <div class="teacherResults_container_content" style="margin-top: 15px;">
                    <p class="zanwu" v-if="achievelist.length === 0">暂无数据</p>
                    <div class="graph">
                        <table cellspacing="0" cellpadding="0" border="0" class="el-table el-table--border">
                            <tr>
                                <th style="text-align:center;">教学目标</th>
                                <th style="text-align:center;">考核总分</th>
                                <th style="text-align:center;">考核平均分</th>
                                <th style="text-align:center;">课程目标达成度</th>
                                <th style="text-align:center;">支撑毕业要求指标点</th>
                                <th style="width:400px;text-align:center;">本课程对该指标点达成度评价值</th>
                            </tr>
                            <tr v-for="(item, index) in achievelist" :key="index">
                                <td>{{item.goalName}}</td>
                                <td>{{item.totalscore}}</td>
                                <td v-if="item.avgscore">{{item.avgscore.toFixed(1)}}</td>
                                <td v-if="item.targetmark">{{item.targetmark.toFixed(2)}}</td>
                                <td>{{item.indexPoint}}</td>
                                <td>{{item.pointMark}}</td>
                            </tr>
                        </table>
                    </div>
                </div>
            </div>
            <div class="teacherResults_container_body">
                <div class="teacherResults_container_title">
                    <span>成绩分析</span>
                </div>
                <div class="teacherResults_container_content">
                    <p class="small-title">班级整体达成情况</p>
                    <div class="graph">
                        <series-bar id="bar-1" :series-bar-data="seriesBarData" chartHeight="350px" :gradual="gradualColor" />
                        <p class="sign-name">成绩分析</p>
                    </div>
                    <div class="graph">
                        <series-bar id="bar-2" :series-bar-data="seriesBarDataAchievement" chartHeight="350px" :gradual="gradualColorTwo" />
                        <p class="sign-name">达成情况分析</p>
                    </div>
                </div>
            </div>
            <!-- <div class="teacherResults_container_body">
                <div class="teacherResults_container_content">
                    <p class="small-title">课程目标达成情况</p>
                    <div class="graph">
                        <line-marker id="line-1" :line-data="lineData" chartHeight="350px" />
                    </div>
                </div>
            </div> -->
            <div class="teacherResults_container_body">
                <div class="teacherResults_container_content" v-for="(vo,index) in scatterData" :key="index">
                    <p class="small-title">{{vo.goalContent}}</p>
                    <div v-if="isScatterShow" class="graph">
                        <scatter :id="`scatter-${index}`" :scatter-data="vo.studentScore" :target-data="targetStr" chartHeight="350px" />
                    </div>
                </div>
            </div>
            <div class="teacherResults_container_body">
                <div class="teacherResults_container_title">
                    <span>改进意见</span>
                </div>
                <div class="teacherResults_container_content">
                    <div class="graph hspace">
                        <el-input type="textarea" :rows="13" placeholder="请在此输入您宝贵的意见..." v-model="form.proposal">
                        </el-input>
                    </div>
                </div>
            </div>
            <div class="teacherResults_container_footer">
                <div @click="scoreAnalysisSaveproposal" class="submit--btn">提交</div>
            </div>
        </div>
        <!-- <ComparisonYears  v-model="ComparisonYearsshow" @close="closeSetCourseDialog"></ComparisonYears> -->
    </div>
</div>
</template>

<script>
import teacherApi from '@/api/teacherApi';
import SeriesBar from '../components/Echarts/SeriesBar'
import Scatter from '../components/Echarts/Scatter'
import LineMarker from '../components/Echarts/LineMarker'
export default {
    name: 'TeacherResults',
    components: {
        SeriesBar,
        Scatter,
        LineMarker,
    },
    data() {
        return {
            falgechange: false,
            pageTitle: '成绩分析',
            activeIndex: 0,
            gradeOptions: [],
            classOptions: [],
            targetStr: [],
            tableHeight: "",
            gradualColor: [{
                    color: '#03B187',
                    gradual: '#8EE9CA'
                },
                {
                    color: '#5AB3FF',
                    gradual: '#B3DCFF'
                },
                {
                    color: '#ECEAFF',
                    gradual: '#D5CFFF'
                }
            ],
            gradualColorTwo: [{
                    color: '#03B187',
                    gradual: '#8EE9CA'
                },
                {
                    color: '#FFA351',
                    gradual: '#FFE098'
                },
                {
                    color: '#5AB3FF',
                    gradual: '#B3DCFF'
                },
                {
                    color: '#ECEAFF',
                    gradual: '#D5CFFF'
                }
            ],
            page: {
                grade: '',
                className: [],
                courseId: '',
                versionId: localStorage.getItem('versionId')
            },
            isShow: false,
            list: [],
            tableData: {},
            seriesBarData: {},
            scatterData: [],
            isScatterShow: false,
            seriesBarDataAchievement: {},
            lineData: {},
            form: {
                proposal: '',
                courseSchedulingId: ''
            },
            courseAnalysis: [],
            achievelist: [],
            teacherlist: []
        }
    },
    created() {},
    mounted() {
        // this.scoreAnalysisGrade()
        this.getclass()
    },
    methods: {
        getclass() {
            const userId = localStorage.getItem('userId')
            this.$get(`/teacher/scoreAnalysis/selectCourseListByTreacherId?teacherId=${userId}`).then(res => {
                if (res.status === 1) {
                    this.teacherlist = res.data
                    this.teacherlist.unshift({
                        id: '',
                        name: '全部 '
                    })
                }
            })
        },
        cahngejump() {
            this.falgechange = !this.falgechange
        },
        initData() {
            let className  = this.page.className.join(",")
            let key = {
                grade: this.page.grade,
                className: className,
                courseId:  this.page.courseId,
                versionId: localStorage.getItem('versionId')
            }
            const url = teacherApi.scoreAnalysisSelectScore(key);
            this.$get(url).then(res => {
                if (res.length === 0) {
                    this.isShow = false;
                    this.$notify.info({
                        duration: 1500,
                        message: '未查到您所需要的内容'
                    })
                } else {
                    this.isShow = true
                    this.isScatterShow = true
                    this.activeIndex = 0
                    this.list = res
                    this.tableData = res[0]
                    this.form.proposal = this.tableData.proposal
                    this.form.courseSchedulingId = this.tableData.courseSchedulingId
                    this.scatterData = this.tableData.scoreGaols
                    this.achievelist = this.tableData.achieve
                    this.courseAnalysis = this.tableData.courseAnalysis
                    let tmpArr = [];
                    this.courseAnalysis.forEach(item => {
                        tmpArr.push(item.result.length);
                    });
                    console.log(Math.max(...tmpArr),'tmpArr')
                    if(Math.max(...tmpArr) < 3) {
                        this.tableHeight = "130px"
                    }else {
                        this.tableHeight = `${Math.max(...tmpArr) * 44 + 44}px`;
                    }
                    this.initEchartData()
                }
            })
        },
        //所属学年
        scoreAnalysisGrade(courseId) {
            //const url =teacherApi.scoreAnalysisGrade;
            const versionId = localStorage.getItem('versionId')
            const url = `/teacher/scoreAnalysis/grade/${versionId}/${courseId}`
            this.$get(url).then(res => {
                this.gradeOptions = res;
            })
        },
        //所属班级
        scoreAnalysisClass(gradeName) {
            const url = teacherApi.scoreAnalysisClass;
            const postData = {
                grade: gradeName
            }
            this.$get(url, postData).then(res => {
                this.classOptions = res;
            })
        },
        educe() {
            // const url=`${this.API_URL}/teacher/courseAnalyze/downmodel?grade=${0}&classId=${this.tableData.classId}&courseId=${this.tableData.courseId}`
            // window.location.href=url
            this.$get(`${this.API_URL}/teacher/courseAnalyze/downmodel?grade=${0}&classId=${this.tableData.classId}&courseId=${this.tableData.courseId}`).then(res => {
                window.location.href = `${this.API_URL}/teacher/courseAnalyze/downmodel?grade=${0}&classId=${this.tableData.classId}&courseId=${this.tableData.courseId}`
            })
            this.falgechange = false
        },
        educe1() {
            //  /teacher/courseGoalExport/getCourseDetail?courseId=580850711307223045&classId=580850147173335040
            let token = localStorage.getItem('x_token')
            const url = `${this.API_URL}/teacher/courseGoalExport/getCourseDetail?classId=${this.tableData.classId}&courseId=${this.tableData.courseId}&x-token=${token}`
            window.location.href = url
            this.falgechange = false
        },
        initEchartData() {
            const self = this
            const echartData = {
                legendData: self.tableData.scoreAnalysis,
                xAxisData: self.tableData.goals,
                seriesData: self.tableData.classScore.map(item => {
                    return {
                        name: item.type,
                        barGap: "5%", //柱状图间距 
                        data: item.scores
                    }
                })
            }
            const achievementData = {
                legendData: self.tableData.achievementAnalysis,
                xAxisData: self.tableData.goals,
                seriesData: self.tableData.achievementScore.map(item => {
                    return {
                        name: item.type,
                        barGap: "5%", //柱状图间距 
                        data: item.reach
                    }
                })
            }
            self.seriesBarData = echartData
            self.seriesBarDataAchievement = achievementData
            //课程目标达成情况(折线图)
            self.lineData = {
                xAxisdData: self.tableData.goals,
                seriesData: self.tableData.achievementScore[0].reach
            }
            //散点标准
            self.targetStr = self.tableData.achievementScore[1].reach[0]
        },
        tabClick(index) {
            this.isScatterShow = false
            this.scatterData = []
            this.activeIndex = index
            this.tableData = this.list[index]
            this.form.proposal = this.tableData.proposal
            this.form.courseSchedulingId = this.tableData.courseSchedulingId
            this.achievelist = this.tableData.achieve
            this.courseAnalysis = this.tableData.courseAnalysis
            let tmpArr = [];
            this.courseAnalysis.forEach(item => {
                tmpArr.push(item.result.length);
            });
            this.tableHeight = `${Math.max(...tmpArr) * 44 + 44}px`;
            this.initEchartData()
            setTimeout(() => {
                this.isScatterShow = true
                this.scatterData = this.tableData.scoreGaols
            }, 500)
        },
        gradeSelect() {
            this.page.className = []
            this.scoreAnalysisClass(this.page.grade)
        },
        courseIdSelect(){
            this.scoreAnalysisGrade(this.page.courseId) 
        },
        search() {
            this.initData()
        },
        headInfo(item) {
            this.pageTitle = item[1]
        },
        scoreAnalysisSaveproposal() {
            const url = teacherApi.scoreAnalysisSaveproposal
            this.$post(url, this.form).then(res => {
                if (res.success) {
                    this.$notify.success({
                        title: 'success',
                        duration: 1500,
                        message: '提交成功'
                    })
                } else {
                    this.$notify.error({
                        title: 'error',
                        duration: 1500,
                        message: '提交失败'
                    })
                }
            })
        }
    }
}
</script>

<style lang="scss">
@import '../../styles/center.scss';

#teacherResults {
    .defaultPage {
        padding: 50px 0 150px;
        box-shadow: inherit;

        p {
            font-size: 20px;
            font-family: PingFang SC;
            font-weight: bold;
            color: rgba(3, 177, 135, 1);
            margin-top: 49px;
        }
    }

    .center_content {
        border-radius: 10px;

        .center_content_search {
            height: 280px;
            background-image: url('../../assets/img/bg-chaxun.png') !important;
            background-size: 100% 100%;
            border-radius: 10px 10px 0px 0px;
            margin-bottom: 0;
            padding-top: 0;

            .t_p {
                font-size: 24px;
                font-weight: bold;
                color: #fff;
                margin-top: 30px;
                text-align: center;
                margin-bottom: 59px;
            }

            .t_s {
                height: 42px;
                width: 100%;
                float: none;
                padding-left: 0;
                position: relative;

                .t_box {
                    // // display:inline-block;
                    // height:100%;
                    // // width:770px;
                    // position:absolute;
                    // position:absolute;
                    // left:0;
                    // right:0;
                    // top:0;
                    // bottom:0;
                    // margin:auto;
                    display: flex;
                    // margin-left: 150px;
                    justify-content: center;//x轴排列
                    align-items: center;//y轴排列
                    .search-input-suffix {
                        // flex: 1;

                        label {
                            font-size: 16px;
                            font-weight: bold;
                            color: #fff;
                        }

                        .el-input__inner {
                            border: 1px solid #fff;
                            color: #fff;
                            background: transparent;
                            font-size: 14px;
                        }

                        input::placeholder {
                            color: #fff;
                        }

                        .el-select__caret {
                            color: #fff;
                        }
                    }
                }

            }

            .t_b {
                height: 50px;
                position: relative;
                margin-top: 40px;

                span {
                    display: inline-block;
                    width: 270px;
                    height: 50px;
                    background: rgba(255, 255, 255, 1);
                    border-radius: 25px;
                    text-align: center;
                    font-size: 18px;
                    font-weight: bold;
                    color: #03B187;
                    line-height: 50px;
                    position: absolute;
                    left: 0;
                    right: 0;
                    top: 0;
                    bottom: 0;
                    margin: auto;
                    cursor: pointer;
                }
            }
        }
    }

    .teacherResults_tab {
        width: 100%;
        min-height: 37px;
        background: #f8f8f8;
        text-align: left;
        clear: both;
        padding: 30px 0;
        .t_btn {
            display: inline-block;
            font-size: 14px;
            color: #696969;
            padding: 10px 16px;
            background: transparent;
            border-radius: 20px;
            margin-bottom: 10px;
            cursor: pointer;
        }

        .active {
            color: #fff;
            background: rgba(3, 177, 135, 1);
            box-shadow: 0px 2px 20px 0px rgba(3, 177, 135, 0.3);
        }

    }

    .teacherResults_container {
        .teacherResults_container_body {
            min-height: 136px;
            background: rgba(255, 255, 255, 1);
            border-radius: 10px;
            padding: 20px;
            margin-bottom: 20px;
        }

        .teacherResults_container_title {
            height: 50px;
            line-height: 50px;
            clear: both;
            position: relative;

            .teacherResults-dv {
                position: absolute;
                top: 33px;
                right: 37px;
                border-radius: 4px;
                border: 1px solid #ebebeb;
                z-index: 10;
                ul {
                    background-color: #fff;

                    li {
                        cursor: pointer;
                        list-style: none;
                        border-bottom: 1px solid #ebebeb;
                        padding: 0 10px;
                        font-size: 14px;
                        color: #323232;

                        &:hover {
                            color: #03B187;
                        }
                    }

                }
            }

            span {
                font-weight: bold;
                font-size: 20px;
                color: #323232;
            }

        }

        .teacherResults_container_content {
            .small-title {
                font-size: 16px;
                color: #323232;
                font-weight: bold;
            }

            .zanwu {
                text-align: center;
                margin-top: 20px;
                letter-spacing: 8px;
            }

            .graph {
                overflow-x: auto;

                .graph_dv1 {
                    text-align: center;
                    height: 100px;
                    line-height: 100px;
                    width: 100px;
                    background-color: #f8f8f8;
                    margin-right: 15px;
                    border-radius: 4px;
                    position: relative;

                    .graph_dvp {
                        width: 20px;
                        margin: 0 auto;
                        line-height: 24px;
                        color: #03b187;
                        font-weight: bold;
                        font-size: 18px;
                        position: absolute;
                        top: 50%;
                        left: 50%;
                        transform: translate(-50%, -50%);
                    }
                }

                .sign-name {
                    line-height: 40px;
                    text-align: center;
                    color: #333;
                    font-size: 14px;
                }

                .el-table--border {
                    td {
                        border-bottom: 1px solid #EBEEF5;
                        text-align: center;
                    }

                    th {
                        background: rgba(248, 248, 248, 1);
                    }
                }
            }

            ul {
                width: 100%;
                margin: 10px 0;

                li {
                    height: 38px;
                    width: 33%;
                    float: left;
                    font-size: 14px;
                    line-height: 38px;
                    list-style-type: none;
                    text-align: left;

                    b {
                        font-weight: normal;
                        color: #323232;
                        margin-right: 5px;
                    }

                    span {
                        color: #323232;
                    }
                }
            }

            .hspace {
                padding: 20px 0;
            }
        }

        .teacherResults_container_footer {
            height: 40px;
            position: relative;

            .submit--btn {
                position: absolute;
                left: 50%;
                margin-left: -140px;
            }

        }
    }
}
</style>



